import { useParams } from "react-router-dom";
import { Alert, Table } from "react-bootstrap";

import { ObjBadge, objName } from "c/ObjBadge";
import {
  MoneySlider,
  AmountSlider,
  PeriodSlider,
  ProbSlider,
  CheckBox,
  fmtPeriodVal,
  fmtMoneyVal,
  fmtProbVal,
} from "c/Sliders";
import { MdText } from "misc/text";

import { actors, threats, vulns, impacts, precs } from "db/contents";

import { useChoices } from "db/choices";
import {
  estThreatTotalPeriod,
  estThreatTotalPeriods,
  estVulnPeriod,
  estMitigatedVulnPeriod,
  estMitigatedVulnPeriods,
  estImpactPeriod,
  estImpactCost,
  getVulnPrecs,
} from "db/estimate";

function DbPage<T extends { label: string }>(
  obj: string,
  collection: Record<string, T>,
  details: (id: string, x: T) => JSX.Element
): JSX.Element {
  const { id } = useParams();
  if (id && id in collection) {
    const x = collection[id];
    return (
      <>
        <h1>
          {x.label} <ObjBadge obj={obj} />
        </h1>
        <p>
          Identifier: <code>{id}</code>
        </p>
        {details(id, x)}
      </>
    );
  } else return <Alert variant="danger">{objName(obj)} entry not found</Alert>;
}

export function Actor() {
  return DbPage(
    "actor",
    actors,
    (id, { description, estimation, estimate }) => (
      <>
        <MdText>{description}</MdText>
        <h5>Generated threats</h5>
        <ul>
          {Object.keys(threats)
            .filter((t) => threats[t].actor === id)
            .map((t) => (
              <li key={t}>
                {threats[t].label} <ObjBadge obj="threat" link={t} />
              </li>
            ))}
        </ul>
        <h2>Actor quantity estimation</h2>
        <MdText>{estimation}</MdText>
        <AmountSlider group="actor" id={id} def={estimate} />
      </>
    )
  );
}

export function Impact() {
  const ch = useChoices();
  const ttps = estThreatTotalPeriods(ch);
  const mvps = estMitigatedVulnPeriods(ch, ttps);

  return DbPage(
    "impact",
    impacts,
    (id, { description, estimation, estimate }) => (
      <>
        <MdText>{description}</MdText>
        <h5>Vulnerabilities that contribute to the impact</h5>
        <ul>
          {Object.keys(vulns)
            .filter((v) => vulns[v].impacts.includes(id))
            .map((v) => (
              <li key={v}>
                {vulns[v].label} <ObjBadge obj="vuln" link={v} />
              </li>
            ))}
        </ul>
        <h5>Impact event statistics</h5>
        <Table className="text-table">
          <tbody>
            <tr>
              <th>Expected time between impact events</th>
              <td>{fmtPeriodVal(estImpactPeriod(ch, id, mvps))}</td>
            </tr>
            <tr>
              <th>Amortized monthly cost of impact events</th>
              <td>{fmtMoneyVal(30 * 86400 * estImpactCost(ch, id, mvps))}</td>
            </tr>
            <tr>
              <th>Amortized yearly cost of impact events</th>
              <td>{fmtMoneyVal(365 * 86400 * estImpactCost(ch, id, mvps))}</td>
            </tr>
            <tr>
              <th>Probability the impact event occurs next month</th>
              <td>
                {fmtProbVal(
                  1 - Math.exp((-86400 * 30) / estImpactPeriod(ch, id, mvps))
                )}
              </td>
            </tr>
            <tr>
              <th>Probability the impact event occurs next year</th>
              <td>
                {fmtProbVal(
                  1 - Math.exp((-86400 * 365) / estImpactPeriod(ch, id, mvps))
                )}
              </td>
            </tr>
          </tbody>
        </Table>
        <h2>Impact estimation</h2>
        <MdText>{estimation}</MdText>
        <MoneySlider group="impact" id={id} def={estimate} />
      </>
    )
  );
}

export function Prec() {
  return DbPage("prec", precs, (id, { description }) => (
    <>
      <MdText>{description}</MdText>
      <h5>Vulnerabilities directly mitigated</h5>
      <ul>
        {Object.keys(vulns)
          .filter((v) => vulns[v].precs.includes(id))
          .map((v) => (
            <li key={v}>
              {vulns[v].label} <ObjBadge obj="vuln" link={v} />
            </li>
          ))}
      </ul>
      <CheckBox group="prec" id={id} />
    </>
  ));
}

export function Threat() {
  const ch = useChoices();

  return DbPage(
    "threat",
    threats,
    (id, { description, estimation, estimate }) => (
      <>
        <MdText>{description}</MdText>
        <h5>Threat causes</h5>
        <p>
          Generated by: {actors[threats[id].actor].label}{" "}
          <ObjBadge obj="actor" link={threats[id].actor} />
        </p>
        <h5>Vulnerabilities exploited by the threat</h5>
        <ul>
          {Object.keys(vulns)
            .filter((v) => vulns[v].threats.includes(id))
            .map((v) => (
              <li key={v}>
                {vulns[v].label} <ObjBadge obj="vuln" link={v} />
              </li>
            ))}
        </ul>
        <h5>Expected occurence</h5>
        <Table className="text-table">
          <tbody>
            <tr>
              <th>Occurence from all actors</th>
              <td>{fmtPeriodVal(estThreatTotalPeriod(ch, id))}</td>
            </tr>
          </tbody>
        </Table>
        <h2>Occurrence estimation</h2>
        <MdText>{estimation}</MdText>
        <PeriodSlider group="threat" id={id} def={estimate} />
      </>
    )
  );
}

export function Vuln() {
  const ch = useChoices();
  const ttps = estThreatTotalPeriods(ch);

  return DbPage(
    "vuln",
    vulns,
    (
      id,
      {
        description,
        threats: ts,
        impacts: is,
        precs: ps,
        amplifies: as,
        estimation,
        estimate,
      }
    ) => {
      const vprecs = getVulnPrecs(id);
      return (
        <>
          <MdText>{description}</MdText>
          <h5>Triggered by threats</h5>
          <ul>
            {ts.map((t) => (
              <li key={t}>
                {threats[t].label} <ObjBadge obj="threat" link={t} />
              </li>
            ))}
          </ul>
          <h5>Causes impacts</h5>
          <ul>
            {is.map((i) => (
              <li key={i}>
                {impacts[i].label} <ObjBadge obj="impact" link={i} />
              </li>
            ))}
          </ul>
          {as ? (
            <>
              <h5>Amplifies vulnerabilities</h5>
              <ul>
                {as.map((v) => (
                  <li key={v}>
                    {vulns[v].label} <ObjBadge obj="vuln" link={v} />
                  </li>
                ))}
              </ul>
            </>
          ) : (
            <></>
          )}
          <h5>Possible precautions and mitigations</h5>
          <ul>
            {vprecs.map((p) => (
              <li key={p}>
                {precs[p].label} <ObjBadge obj="prec" link={p} />
              </li>
            ))}
          </ul>
          <h5>Expected occurence</h5>
          <Table className="text-table">
            <tbody>
              <tr>
                <th>Without mitigation</th>
                <td>{fmtPeriodVal(estVulnPeriod(ch, id, ttps))}</td>
              </tr>
              <tr>
                <th>With current mitigations</th>
                <td>{fmtPeriodVal(estMitigatedVulnPeriod(ch, id, ttps))}</td>
              </tr>
            </tbody>
          </Table>
          <h2>Exploitability estimation</h2>
          <MdText>{estimation}</MdText>
          <ProbSlider group="vuln" id={id} def={estimate} />
        </>
      );
    }
  );
}
